<v-row no-gutters align="end"><v-col cols="11"><v-snackbar v-model="snackbar" color="error" :timeout="3000" close-on-content-click location="top">Cannot send message to all agents, please select an agent or group</v-snackbar><v-textarea id="messageInput" v-model="messageInput" label="Message" auto-grow :rows="1" :max-rows="5" @keydown.enter="handleEnter" :disabled="!enabled" clearable></v-textarea></v-col><v-col class="d-flex align-center justify-space-between" cols="1"><v-btn class="btn" icon @click="sendMessage" :disabled="!enabled"><v-icon>mdi-send</v-icon></v-btn><v-menu v-model="menu" :close-on-content-click="false" offset-y><template v-slot:activator="{ props }"><v-btn class="btn" icon v-bind="props"><v-icon>mdi-dots-vertical</v-icon></v-btn></template><v-card min-width="225" height="65"><v-list><v-list-item><v-row><v-col cols="9"><v-list-item-title class="scrollOnMessageTitle">Scroll on Message</v-list-item-title></v-col><v-col cols="3"><v-list-item-action><v-switch v-model="preferences.scrollOnMessage" density="compact"></v-switch></v-list-item-action></v-col></v-row></v-list-item></v-list></v-card></v-menu></v-col></v-row>