<v-app-bar app color="primary" dark><v-toolbar-title>{{ targetSelected?.name || 'All' }}</v-toolbar-title></v-app-bar><div class="chat-log"><ul v-if="localMessages.length"><li v-for="(messageInput, index) in localMessages" :key="messageInput.id" :ref="setLastMessageRef"><span class="source">{{ messageInput.source }}:</span><span class="content">{{ messageInput.content }}</span><span class="timestamp">{{ new Date(messageInput.timestamp).toLocaleTimeString() }}</span></li></ul></div>