<v-card><h3>Groups</h3><v-list v-if="groups.length"><v-list-item v-for="group in groups" :key="group.name" @click="selectChat(group.name, 'group')" :class="{ 'selected-chat': group.name === targetSelected.name, badge: true }"><v-badge v-if="group.count &gt; 0" :content="group.count" floating offset-y="5" offset-x="-5" label="{{group.name}}" color="#f0f0f0">{{ group.name }}</v-badge><span v-else>{{ group.name }}</span></v-list-item><v-list-item><v-btn icon="mdi-plus" @click="showAddGroupDialog = true" density="compact"></v-btn><v-dialog v-model="showAddGroupDialog" max-width="290"><v-card><v-card-title><span class="headline">Add Group</span></v-card-title><v-card-text><v-text-field v-model="newGroupName" label="Group Name" autofocus="true"></v-text-field></v-card-text><v-card-actions><v-spacer></v-spacer><v-btn text @click="showAddGroupDialog = false">Cancel</v-btn><v-btn color="blue darken-1" text @click="addGroup">Add</v-btn></v-card-actions></v-card></v-dialog></v-list-item></v-list><h3>Agents</h3><v-list v-if="agents.length"><v-list-item v-for="agent in agents" :key="agent.name" @click="selectChat(agent.name, 'agent')" :class="{ 'selected-chat': agent.name === targetSelected.name, badge: true }"><v-badge v-if="agent.count &gt; 0" :content="agent.count" floating offset-y="5" offset-x="-5" label="{{agent.name}}" color="#f0f0f0">{{ agent.name }}</v-badge><span v-else>{{ agent.name }}</span></v-list-item><v-list-item><v-btn icon="mdi-plus" @click="showAddAgentDialog = true" density="compact"></v-btn><v-dialog v-model="showAddAgentDialog" max-width="290"><v-card><v-card-title><span class="headline">Add Agent</span></v-card-title><v-card-text><v-text-field v-model="newAgentName" label="Agent Name" autofocus="true"></v-text-field><v-select v-model="selectedDriver" :items="drivers" label="Driver"></v-select></v-card-text><v-card-actions><v-spacer></v-spacer><v-btn text @click="showAddAgentDialog = false">Cancel</v-btn><v-btn color="blue darken-1" text @click="addAgent">Add</v-btn></v-card-actions></v-card></v-dialog></v-list-item></v-list></v-card>